.App {
  position: fixed;
  /* background-color: red; */

  right: 0;
  bottom: 0;
  margin: 50px;
  /* height: 100px; */
  /* width: 100px; */
  /* padding: 50px; */
}
.container {
  display: flex;
  align-items: center;
}
.circle {
  height: 60px;
  width: 60px;
  padding: 15px;
  align-self: center;
  /* background-color: #f9d147; */
  border-radius: 50%;
  border: solid 4px #2ba272;
  display: flex;
  justify-content: center;
  align-items: center;
  /* font-size: 2.3em; */
}
.message {
  margin-right: 4px;
  font-size: 1.4em;
  background-color: #a9a9a9;
  padding: 8px 30px;
  height: 40px;
  border-radius: 8px;
  display: flex;
  color: #fefefe;
  align-items: center;
}
